html {
  box-sizing: border-box;
}


/*******************************************/

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: normal normal normal 1rem/1.6 -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
}



/*fondo de la pagina */
body {
  color: #404040;
  background-image: url(https://img.blogs.es/anexom/wp-content/uploads/2020/08/pinterest-fondos-1.jpg);
  background-size: cover;

  font-size: 1rem;
}


/*formato TITULO */
h1 {
    margin: 1.5rem 0;
    font-weight: 00;
    font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
    line-height: 1.2;
    color: #0e0c0c;
  }


/*Color fuente de titutlos y subtitles */
h2,
h3,
h4,
h5 {
  margin: 1.5rem 0;
  font-weight: 00;
  font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  line-height: 1.2;
  color: #f00897;
}





/* margenes h1 titulos y sub titules*/
@media (min-width: 600px) {
  h1:not(:first-child),
  h2:not(:first-child),
  h3:not(:first-child) {
    margin-top: 2.5rem;
  }
  h1 {
    font-size: 2.25rem;
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1.5rem;
  }
  h5 {
    font-size: 1.25rem;
  }
}


.container, .small-container, .medium-container {
  max-width: 1200px;
  padding: 0 1rem;
  margin-left: auto;
  margin-right: auto;
}


/*grip o espacio entre api*/
.flex-small,
.flex-large {
  padding-left: 2rem;
  padding-right: 2rem;
}

/*garnenes */
.flex-row {
  margin-left: -1rem;
  margin-right: -1rem;
}



/* estilo de botnoes update user */

.button, a.button, button, [type=submit], [type=reset], [type=button] {
  -webkit-appearance: none;
  display: inline-block;
  border: 1px solid #2c2c2c;
  border-radius: 4px;
  background: #7f7f80;
  color: #f8f8f7;
  font-weight: 600;
  font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  text-transform: none;
  padding: .55rem 1.0rem;
  margin: 0.5rem -0.2rem -0.2rem 0.8rem;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  line-height: 1;
}

/* botton add new user pasa por encima */

.button:hover, a.button:hover, button:hover, [type=submit]:hover, [type=reset]:hover, [type=button]:hover {
  border: 1px solid #2c2c2c;
  background: #b3b3b3;
  color: #f8f8f7;
  text-decoration: none;
}

/*boton cancelar, edit delte */
.muted-button,
a.muted-button {
  background: transparent;
  border: 1px solid #0e0e0e;
  background: #74a5b8;
  color: #ffffff;;
}

/*pasar por encima boton cancelar, edit delte */
.muted-button:hover, .muted-button:focus, .muted-button:active,
a.muted-button:hover,
a.muted-button:focus,
a.muted-button:active {
  color: #ffffff;
  border: 1px solid #cfcece;
  background: #d0f2f3;
}



/* Forms  Estilos de text area*/
[type=color], [type=date], [type=datetime], [type=datetime-local], [type=email], [type=month], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url], [type=week], [type=time], select, textarea {
  display: block;
  border: 1px solid #353131;
  border-radius: 4px;
  padding: 0.75rem;
  outline: none;
  background: rgb(255, 255, 255, 0.6);
  margin-bottom: 0.5rem;
  font-size: 1rem;
  width: 100%;
  max-width: 100%;
  line-height: 1;
}

/* Forms  Estilos de text area pasar por encima*/

[type=color]:hover, [type=date]:hover, [type=datetime]:hover, [type=datetime-local]:hover, [type=email]:hover, [type=month]:hover, [type=number]:hover, [type=password]:hover, [type=search]:hover, [type=tel]:hover, [type=text]:hover, [type=url]:hover, [type=week]:hover, [type=time]:hover, select:hover, textarea:hover {
  border: 1px solid #fd3793;
}


/* margen text area*/
textarea {
  overflow: auto;
  height: auto;
}

select::-ms-expand {
  display: none;
}

/* magern label edit user */

label {
  font-weight: 600;
  max-width: 100%;
  display: block;
  margin: 1rem 0 0.5rem;
}

/** Tables formato */
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  max-width: 100%;
  background: rgba(255, 255, 255, 0.4);
  color: #030303;
}

/* Agrega primero linea a la tabl*/
thead th {
  border-bottom: 2px solid #ffffff;
}


/* formato de linea entre objet  de la litas*/
td {
  border-bottom: 1px solid #ffffff;
}


/* espacios entre objeto de la lista*/

th,
td {
  text-align: left;
  padding: 0.5rem;
}



